/* COLORS */
/* Attributes */
/**
 * Styling for the Red Pill screen (the BitNode selection UI)
 */
#red-pill-container {
  position: fixed; }

.bitnode.level-0 {
  color: red; }

.bitnode.level-1 {
  color: yellow; }

.bitnode.level-2 {
  color: #48d1cc; }

.bitnode.level-3 {
  color: blue; }

.bitnode.unimplemented {
  color: gray; }

.bitnode:hover {
  color: #fff; }
