.optionRange::-webkit-slider-thumb {
  -webkit-appearance: none;
  appearance: none;
  width: 10px;
  height: 10px;
  background: var(--my-font-color);
  cursor: pointer; }

.optionRange::-moz-range-thumb {
  width: 10px;
