  cursor: pointer; }

.noselect, br, .dialog-box-close-button {
  -moz-user-select: -moz-none;
  -khtml-user-select: none;
  -webkit-user-select: none;
  -ms-user-select: none;
  user-select: none; }

/* Pop-up boxes */
.popup-box-container {
  display: none;
  /* Initially hidden */
  position: fixed;
  /* Stay in place */
  z-index: 1300;
  /* Sit on top */
  left: 0;
  top: 0;
  align-items: center;
  justify-content: center;
  width: 100%;
  height: 100%;
  background-color: rbga(var(--my-background-color), 0.4); }

.popup-box-content {
  background-color: var(--my-background-color);
  padding: 12px;
  border: 5px solid var(--my-highlight-color);
  width: 70%;
  max-height: 80%;
  overflow-y: auto;
  z-index: 11;
  /* Sit on top of the container */
  color: var(--my-font-color); }

.popup-box-input-div {
