  padding: 2px;
  margin: 6px;
  border: 1px solid #fff;
  background-color: #000; }

.popup-box-button:hover,
.popup-box-button:focus {
  color: var(--my-font-color);
  text-decoration: none;
  cursor: pointer; }

.popupbox-button-inactive {
  pointer-events: none;
  cursor: default; }

#yes-no-text-input-box-input {
