  flex-flow: column;
  background-color: gray;
  width: 50%;
  position: fixed;
  left: 50%;
  top: 40%;
  margin: -10% 0 0 -25%;
  height: auto;
  max-height: 50%;
  z-index: 10;
  background-color: var(--my-background-color);
  border: 2px solid var(--my-highlight-color); }

.log-box-header {
